define('libertyjs-dev/router', ['exports', 'ember', 'libertyjs-dev/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('schedule');
    this.route('tickets');
    this.route('callforproposals');
    this.route('sponsorship');
    this.route('sponsors');
    this.route('codeofconduct');
    this.route('volunteer');
    this.route('speakers');
    // this.route('speakers', { path: '/speakers/:speaker_id' });
  });

  exports['default'] = Router;

});